<template>
  <div class="v-select-language--section v-section container">
    <img
      :src="
        require(`@/assets/product-images/193fad73376d41db8ff6295d1ac55493.png`)
      "
    />
    <div class="v-grid v-right-flexed row">
      <div class="v-col col-md-7">
        <div class="v-flags">
          <h1>{{ headlines }}</h1>
          <ul class="v-flex">
            <li v-for="value in languages" :key="value.CountryImg">
              <a
                class="btn btn-light v-flex"
                @click.stop.prevent="switchLocale(value)"
              >
                <img
                  :src="require(`@/assets/product-images/${value.CountryImg}`)"
                />
                <span> {{ value.ButtonText }} </span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import trackingEvents from '../tracking';

export default {
  name: 'ChooseLanguages',
  data() {
    return {
      headlines: 'PLEASE SELECT YOUR LANGUAGE',
      countryImg: '',
      buttonText: '',
      languages: [],
    };
  },
  mounted() {
    this.$store.commit('resetLocaleList');
    this.loadData();
    trackingEvents.emitMtmEvent({
      event: 'app.page',
      'app.page': '/AppStart',
    });
  },
  computed: {},
  methods: {
    switchLocale(link) {
      if (this.$root.$i18n.locale !== link.Locale) {
        this.$root.$i18n.locale = link.Locale;
      }
      this.$store.commit('setLocale', link.Locale);
      this.$store.commit('setQuestionsFile', link.QuestionnaireFileName);
      this.$store.commit('setProductsFile', link.ProductsFileName);
      localStorage.setItem('pf-questions', link.QuestionnaireFileName);
      localStorage.setItem('pf-products', link.ProductsFileName);
      trackingEvents.emitMtmEvent({
        event: 'selectLanguage',
        'app.language': link.Locale,
      });
      this.$router.push({ name: 'questions' });
    },
    loadData() {
      const pathToData = 'data/config.json';
      fetch(pathToData)
        .then((response) => response.json())
        .then((data) => this.initData(data));
    },
    initData(data) {
      this.languages = data.LanguageSelections;
      const localeList = this.languages.map((x) => x.Locale);
      this.$store.commit('setLocaleList', localeList);
      const currentLocale = this.$store.getters.locale;
      const language = this.languages.filter((x) => x.Locale === currentLocale);
      this.headlines = language[0]?.Title;
      this.tracking(language[0]);
    },
    tracking(language) {
      console.log('init data', language, {
        event: 'setStoreId',
        'app.storeId': language.pharmacy_id,
      });

      trackingEvents.emitMtmEvent({
        event: 'setStoreId',
        'app.storeId': language.pharmacy_id,
      });
      trackingEvents.emitMtmEvent({
        event: 'setLocation',
        'app.location': language.country_name,
      });
      trackingEvents.emitMtmEvent({
        event: 'setStoreName',
        'app.storeName': language.pharmacy_name,
      });
    },
  },
};
</script>

<style scoped lang="scss">
.v-select-language--section > img {
  left: 0;
  position: absolute;
}

.v-product-info--section li {
  border-radius: 20px;
  overflow: hidden;
}

h1 {
  color: #4e5c67;
}

ul {
  list-style-type: none;
  padding: 0;
}

.v-flags {
  padding-bottom: 40px;
  padding-top: 40px;

  .btn {
    padding-bottom: 1px;
  }
}

.v-flags h1 {
  margin-bottom: 30px;
}

.v-flags .btn img {
  width: 70px;
}
.v-flags .v-flex {
  align-items: center;
  display: flex;
  flex-flow: wrap;
}

.v-flags .btn span {
  color: #a70632;
  text-align: left;
  padding-bottom: 3px;
  padding-left: 20px;
  width: 100%;
}

.v-flags a {
  display: flex;
  flex-flow: row;
  width: 100%;
}

.v-flags li {
  flex: 1 0 50%;
  padding: 0 10px;

  &:nth-child(n + 3) {
    margin-top: 20px;
  }
}

.v-flags .btn-light {
  box-shadow: 12px 11px 20px 0px #e4e4e4;
  flex-flow: row;
}

@media screen and (min-width: 640px) {
  .v-flags .btn span {
    font-size: 32px;
    line-height: 32px;
  }
}

@media screen and (min-width: 1500px) {
  .v-flags .btn span {
    font-size: 36px;
    line-height: 36px;
  }
}

@media screen and (max-width: 959px) {
  .v-flags .btn span {
    font-size: 28px;
    line-height: 28px;
  }

  .v-flags .v-flex {
    flex-wrap: wrap;
  }

  .v-flags .btn span {
    font-size: 24px;
    line-height: 24px;
    padding-top: 10px;
    padding-left: 0;
    text-align: center;
  }
}
</style>
